<template>
  <div class="payrecords">
    <div class="user-header">
      <a class="nav-item1" @click="payBack"><img class="game_1" src="../../assets/images/usercenter/game_1.png"/></a>
      <a class="ordertails">充值记录</a>
    </div>
    <div class="payrecordsMain">
      <div class="noRecord" v-if="isNoRecord">暂无充值记录</div>
      <div v-else >
        <div class="mailOrder" @touchmove="move" @touchstart="start" v-for="(item,index) in payList">
          <div class="orderHead">
            <a>订单号:{{item.channel_order}}</a>
            <div><span>{{item.num}}</span>钻 <a v-if="isDiamond">( 送{{item.extraNum}}钻 )</a><a v-else>(送{{item.extraNum}}积分)</a></div>
          </div>
          <div class="riqi">
            <a>{{item.start_time}}</a>
            <a class="price">￥{{item.price}}</a>
          </div>
          <div class="delete" @click="deleteRecord(item,index)">删除</div>
        </div>
        <div class="loadMore" @click="getMore" v-show="isMore">点击加载更多</div>
        <div class="noMore" v-show="!isMore">没有更多记录了</div>
      </div>
    </div>
    <myModal text_l="不删了" text_r="删了" mes="确定删除该条充值记录?" @childSay="listenToMyBoy" @deletesure="deleteSure" v-show="modalShow"></myModal>
  </div>
</template>
<script>

  var urlhost = process.env.API_AJAX;

  //引入模态层
  import Modal from '../modals/modalStart.vue'
  export default {
    name:"payrecords",
    data() {
      return {
        payList:[],
        isDiamond:true,
        modalShow:false,
        isMore:true,     //点击加载更多按钮
        clickNum:1,      //记录点击次数，获取更多数据
        startX:0,
        startY:0,
        moveEndX:0,
        moveEndY:0,
        X:0,
        Y:0,
        id:'',
        idx:'',
        isNoRecord:null,
      }
    },
    components: {
      myModal:Modal
    },
    mounted:function () {
      var _this = this;
      var userId = getCookie("userId");
      $.ajax({
        type: 'GET',
        url: urlhost+"usercenter/payrecord?num=10&user_id="+userId,
        data$Type: "jsonp",
        data: {
        },
        success: function (response) {
          var mess = JSON.parse(response);
          var mypay = mess.message;
          if(mypay==''){
            _this.isNoRecord = true;
          }else{
            _this.isNoRecord = false;
            for(var i=0;i< mypay.length;i++) {
              var num = parseInt(mypay[i].end_time);
              var time = _this.format(num);
              mypay[i].start_time = time;
              mypay[i].price = mypay[i].discount;
              mypay[i].getNum = mypay[i].num;
              mypay[i].extraNum = mypay[i].integral;

              //判断钻石/积分
              if(mypay[i].topup_type != 1){
                _this.isDiamond = false;
              }
            }
          }
        _this.payList = mypay;
        },
      });
    },
    methods:{
      //获取时间
      format:function(timestamp){
        var date = new Date(timestamp * 1000);//时间戳为10位需*1000，时间戳为13位的话不需乘1000
        var Y = date.getFullYear() + '/';
        var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '/';
        var D = date.getDate() + ' ';
        var h = date.getHours() + ':';
        var m = date.getMinutes() < 10 ? '0'+(date.getMinutes()) : date.getMinutes();
        return Y+M+D+h+m;
      },
      payBack:function () {
        this.$router.go(-1);
      },
      start:function (e) {
        var _this = this
        _this.startX = e.targetTouches[0].pageX;
        _this.startY = e.targetTouches[0].pageY;
      },
      move:function (e) {
        var _this = this;
        _this.moveEndX = e.targetTouches[0].pageX,
          _this.moveEndY = e.targetTouches[0].pageY,
          _this.X = _this.moveEndX - _this.startX,
          _this.Y = _this.moveEndY - _this.startY;
        if ( Math.abs(_this.X) > Math.abs(_this.Y) && _this.X > 0 ) {
          $(e.currentTarget.children[2]).animate({'width':0},100);
          $(e.currentTarget).animate({'padding-right':'10px'},100)
        }
        else if ( Math.abs(_this.X) > Math.abs(_this.Y) && _this.X < 0 ) {
          $(e.currentTarget.children[2]).animate({'width':'1.5rem'},100)
          $(e.currentTarget).animate({'padding-right':'1.8rem'},100)
        }

      },
      deleteRecord:function(data,i) {
        this.modalShow = true;
        this.id = data.id;
        this.idx = i;
      },
      listenToMyBoy:function () {
        this.modalShow = false;
        $('.delete').animate({"width":0},10);
        $('.mailOrder').animate({'padding-right':'10px'},10)
      },
      deleteSure:function(data,item){
        var _this = this;
        _this.modalShow = false;
        var userid = getCookie("userId");
        $.ajax({
          type: 'POST',
          url: urlhost + "usercenter/payrecorddel?user_id=121&order_id="+_this.id,
          data$Type: "jsonp",
          success: function (res) {
            var res = JSON.parse(res);
            if(res.status == 0){
              _this.$layer.msg(res.message);
            }else{
              _this.$layer.msg(res.message);
              _this.payList.splice(_this.idx, 1);

              //判断payList长度
              if(_this.payList.length==0){
                _this.isNoRecord=true;
              }
            }
          },
        })
      },

      //点击更多按钮
      getMore:function(){
        this.clickNum++;
        var page = this.clickNum;
        var userid = getCookie("userId");
        var url = urlhost+"/usercenter/payrecord";

        $.post(url,{"page":page,"user_id":121,'num':10},{emulateJSON:true}).then((res)=>{
          var mess = JSON.parse(res);
          var mypay = mess.message;
          var mode = mess.mode;

          //判断请求状态为0时 或者 请求的数组长度小于等于10时
          if(mess.status == 0){
            this.isMore = false;
          }
          for(var i=0;i< mypay.length;i++) {
            var num = parseInt(mypay[i].end_time);
            var time = this.format(num);
            mypay[i].start_time = time;
            mypay[i].price = mypay[i].discount;
            mypay[i].getNum = mypay[i].num;
            mypay[i].extraNum = mypay[i].integral;

            //判断钻石/积分
            if(mypay[i].topup_type != 1){
              this.isDiamond = false;
            }

            //将新请求的数据逐一添加至原来的数组中
            this.payList.push(mypay[i]);

          }

        }).catch((res)=>{})
      },

    }
  }
</script>
<style lang="scss" scoped>
  @function n($n) {
    @return $n  / 64 + rem;
  };
  .payrecords {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0px;
    overflow: scroll;
    background-color: #f2f2f2;
    .user-header {
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: n(80);
      background: #fff001;
      display: flex;
      line-height: n(80);
      z-index: 999;
      border-bottom: 1px solid #d8d8d8;
      .nav-item1 {
        position: absolute;
        left: 0.2rem;
        .game_1 {
          width: n(50);
          height: n(50);
        }
      }
      .ordertails {
        font-size: 14px;
        font-weight: bold;
        width: 100%;
        display: inline-block;
        text-align: center;
      }
    }
    .payrecordsMain{
      margin-top: n(80);
      width: 100%;
      overflow: hidden;
      .noRecord{
        text-align: center;
        height: 100%;
        margin-top: 50%;
      }
      div{
        .mailOrder{
          position: relative;
          margin-top: 10px;
          background: #ffffff;
          padding:0 15px;
          height:  n(100);
          .orderHead{
            width: 100%;
            display: inline-flex;
            justify-content: space-between;
            align-items: flex-end;
            height: n(48);
            div{
              span{
                font-size: n(26);
              }
            }
          }
          .riqi{
            width: 100%;
            display: inline-flex;
            justify-content: space-between;
            align-items: center;
            .price{
              font-size: n(28);
              color: #ff6a07;
            }
          }
          .delete{
            position: absolute;
            top:0;
            right: 0;
            height: 100%;
            display: inline-flex;
            justify-content: center;
            align-items: center;
            background: #ff4e00;
            color: #ffffff;
            width: n(0);
          }
        }
        .loadMore{
          height:n(60);
          line-height: n(60);
          width: 100%;
          text-align: center;
          color: #1b1a1a;
          background: #fff001;
          font-weight: bold;
        }
        .noMore {
          height: n(50);
          line-height: n(50);
          width: 100%;
          text-align: center;
          color: #666;
        }
      }


    }
  }
</style>




